"use client";

import { Button } from "@/components/ui/button";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableFooter,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { useQuery, useMutation } from "@tanstack/react-query"
import axios from "axios"
import { toast } from "sonner";

export default function DoctorsPage() {
  const { data: doctors, isLoading, error } = useQuery({
    queryKey: ['doctors'],
    queryFn: async () => {
      const doctors = await axios.get("/api/admin/users/doctors/")
      return doctors.data
    }
  })

  const deleteDoctor = useMutation({
    mutationFn: async (id: string) => {
      const response = await axios.delete(`/api/admin/users/${id}`)
      return response.data
    },
    onSuccess: (data: any) => {
      toast.success("Doctor deleted successfully")
    },
    onError: (err: any) => {
      toast.error("Failed to delete doctor")
    }
  })

  if (isLoading) return <div>Loading...</div>
  if (error) return <div>Error: {error.message}</div>

  return (
    <div>
      <h1>Doctors</h1>
      <Table>
        <TableCaption>A list of doctors registered in the system.</TableCaption>
        <TableHeader>
          <TableRow>
            <TableHead className="w-[100px]">Doctor ID</TableHead>
            <TableHead>Doctor Name</TableHead>
            <TableHead>Email</TableHead>
            <TableHead>Phone</TableHead>
            <TableHead>Experience</TableHead>
            <TableHead>Address</TableHead>
            <TableHead>Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {doctors?.map((doctor: any) => (
            <TableRow key={doctor.id}>
              <TableCell className="font-medium">{doctor.id.toString().slice(0, 6)}</TableCell>
              <TableCell>{doctor.name}</TableCell>
              <TableCell>{doctor.email}</TableCell>
              <TableCell>{doctor.phone}</TableCell>
              <TableCell>{doctor.experience}</TableCell>
              <TableCell>{doctor.address}</TableCell>
              <TableCell>
                <Button variant="destructive" onClick={() => deleteDoctor.mutate(doctor.id)}>
                  Delete
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  )
}